import React from 'react' 
import { NavLink } from 'react-router-dom'
import MetaTags from 'react-meta-tags';
  
function Home(props) { 

    console.log(props.data)
    // const homeAdv = props.data.items

    function renderList() {
        return props.linksList.map( (item, index) =>   (
            <div  key={index} className="col-12 col-sm- col-lg-3" >
                <div className="card d-flex flex-column align-items-center" >
                <img src={`https://andrewprokopenko.github.io/drop1-admin/build/images/${item.img}`} className="card-img-top mt-2 img-60" alt="..." style={{ width: 100, height: 100}}/>
                
                    <div className="card-body">
                        <h2 className="card-title">{item.title}</h2>
                        <h3 className="card-subtitle mb-2 text-muted d-flex flex-column">
                            {   
                                item['list-service-menu'] && 
                                item['list-service-menu'].map( (innerItem) => (
                                    <NavLink to={`/${item.slug}/${innerItem.slug}`}>
                                        { innerItem.title}
                                    </NavLink>
                                ))
                            }
                        </h3> 
                    </div>
                </div>
            </div>
        ))
    }
    function renderAdv() {
        return  props.data.items.map( (item, index) =>   ( 
            <div  key={index} className="col-12 col-sm- col-lg-3" >
                <div className="card p-3 d-flex flex-column align-items-center" >


                        {/* <div className='img-mask' style={{ 
                            ['-webkitMask']: `url(./images/${item.img}) no-repeat center`,
                            mask: `url(https://andrewprokopenko.github.io/drop1-admin/build/images/${item.img}) no-repeat center`,
                            
                            backgroundPosition: 'center', 
                            backgroundRepeat: 'no-repeat',
                            backgroundSize: 'contain',
                            backgroundColor: "#666"
                        }}></div> */}
 
                    <img src={`https://andrewprokopenko.github.io/drop1-admin/build/images/${item.img}`} className="card-img-top img-60" alt="..." style={{ width: 100, height: 100}}/>
                    <div className="card-body">
                        <h2 className="card-title">{item.title}</h2> 
                    </div>
                </div>
            </div>
        ))
    }
 
    return (
        <React.Fragment>
        { 
            props.data && 
            <div className="site-container">
                <MetaTags>
                    <title>{props.data.meta.title}</title> 
                    <metNavLink name="description" content={props.data.meta.description} /> 
                </MetaTags>
                <main role="main" >
                    <div className="container">
                        <div className='row mt-3'>
                            {
                                renderList() 
                            } 
                        </div> 
                        <div className='row mt-5'>
                            {
                                renderAdv() 
                            } 
                        </div>  
                        <div dangerouslySetInnerHTML={{__html:  props.data.content}}></div>
                    </div> 
                </main> 
            </div>
        }
        <span></span> 
        </React.Fragment>
    )
}

export default Home
