import React from 'react'
import Breadcrumb from '../Breadcrumbs'

import MetaTags from 'react-meta-tags';
 

function SinglePageService(props) { 
     
    const content = props.data.content
    const meta = props.data.meta
    let breadcrumbsInner = props.breadInner || {}

    console.log(breadcrumbsInner)
    let breadcrumbs = [
        {
            name: 'Главная',
            slug: ''
        } 
    ]
    if(breadcrumbsInner.hasOwnProperty('title')) {
        breadcrumbs.push({
            name: breadcrumbsInner.title,
            slug: breadcrumbsInner.slug,
        })
    }

    // Последняя неактивная крошка 
    breadcrumbs.push({
        name: props.data.title,
        slug: props.data.slug, 
        lastChild: true
    })
      
    return (
        <React.Fragment> 
            <MetaTags>
                <title>{meta.title}</title> 
                <meta name="description" content={meta.description} /> 
            </MetaTags>
 
            <Breadcrumb items={breadcrumbs} /> 
           
            <main role="main">
                <div className="container" dangerouslySetInnerHTML={{__html: content}}></div>
            </main>
   
        </React.Fragment>
    )
}

export default SinglePageService
