import React from 'react' 
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';   
import axios from "axios"
// import data from './data/database.json' 

import './assets/style/style.scss' ; 
  
import Header from './Components/Header'
import Footer from './Components/Footer'
 
import Home from './Components/Home' 

import SinglePageService from './Components/SinglePageService';
  
import ErrorPage from './Components/ErrorPage'  
  
import ScrollToTop from './ScrollToTop'

 
function RouterComponent() {  
    let [data, setData] = React.useState([])
    let [linksList, setLinksList] = React.useState([])
    let [homeList, setHomeList] = React.useState([])

    
    React.useEffect( async () => { 
        await axios.get('https://andrewprokopenko.github.io/drop1-admin/build/data/database.json')
            .then( (response) => {
                console.log(response)
                setData(response.data)
                setLinksList([
                    response.data['remont-kompyutepov'],
                    response.data['remont-noutbukov'],
                    response.data['remont-monitorov'],
                    response.data['nastrojka-po'],
                    response.data['contacts']
                ])
                setHomeList([
                    response.data['remont-kompyutepov'],
                    response.data['remont-noutbukov'],
                    response.data['remont-monitorov'],
                    response.data['nastrojka-po']
                ])
            } )
    }, [])
 
   
 
    return (      
           <div className="mb-4"> 
                    <Router basename="/drop1" > 
                    
                            <ScrollToTop/>
                            
                            <Header linksList={linksList} />  
                            <Switch>
                                <Route exact path='/' render={() => <Home linksList={homeList} data={data['main-list']} /> } />

                                {
                                    linksList.map((item, index) => (
                                        <Route key={index} exact path={`/${item.slug}`} render={() => <SinglePageService linksList={linksList} data={item} /> } />
                                    ))
                                }
                                {
                                    linksList.map((item) => {
                                        return item['list-service-menu'] && item['list-service-menu'].length > 0 &&
                                            item['list-service-menu'].map( (innerItem, index) => { 
                                                return <Route key={index} path={`/${item.slug}/${innerItem.slug}`} render={() => <SinglePageService breadInner={item} data={innerItem} /> }/>
                                            })
                                    })
                                }
                                {
                                    linksList.map((item) => {
                                        return item['list-brands-menu'] && item['list-brands-menu'].length > 0 &&
                                            item['list-brands-menu'].map( (innerItem, index) => { 
                                                return <Route key={index} path={`/${item.slug}/${innerItem.slug}`} render={() => <SinglePageService breadInner={item} data={innerItem} /> }/>
                                            })
                                    })
                                }
                                 
                                <Route  path='*' component={ErrorPage} />

                            </Switch>
                            <Footer/>
                    </Router>  
            </div>  
      
    )
}

export default RouterComponent

